.tlx-fab-bar-overlay {
  transition: opacity 0.2s;
}

.tlx-fab-bar-overlay.tlx-fab-bar-overlay--open {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 1;
}

.tlx-fab-bar-overlay.tlx-fab-bar-overlay--closed {
  opacity: 0;
}

.tlx-fab-bar {
  position: fixed;
  display: flex;
  align-items: center;
  font-family: 'Roboto', sans-serif;
}

.tlx-fab-bar--open .tlx-material-fab-button.secondary:nth-child(1) {
  transition-delay: 0s;
}

.tlx-fab-bar--open .tlx-material-fab-button.secondary:nth-child(2) {
  transition-delay: 0.04s;
}

.tlx-fab-bar--open .tlx-material-fab-button.secondary:nth-child(3) {
  transition-delay: 0.08s;
}

.tlx-fab-bar--open .tlx-material-fab-button.secondary:nth-child(4) {
  transition-delay: 0.12s;
}

.tlx-fab-bar--open .tlx-material-fab-button.secondary:nth-child(5) {
  transition-delay: 0.16s;
}

.tlx-fab-bar--open .tlx-material-fab-button.secondary:nth-child(6) {
  transition-delay: 0.2s;
}

.tlx-fab-bar--closed .tlx-material-fab-button.secondary:nth-child(6) {
  transition-delay: 0.04s;
}

.tlx-fab-bar--closed .tlx-material-fab-button.secondary:nth-child(5) {
  transition-delay: 0.08s;
}

.tlx-fab-bar--closed .tlx-material-fab-button.secondary:nth-child(4) {
  transition-delay: 0.12s;
}

.tlx-fab-bar--closed .tlx-material-fab-button.secondary:nth-child(3) {
  transition-delay: 0.16s;
}

.tlx-fab-bar--closed .tlx-material-fab-button.secondary:nth-child(2) {
  transition-delay: 0.2s;
}

.tlx-fab-bar--closed .tlx-material-fab-button.secondary:nth-child(1) {
  transition-delay: 0.24s;
}

.tlx-fab-bar--link-container {
  display: flex;
}

.tlx-fab-bar--right,
.tlx-fab-bar--right .tlx-fab-bar--link-container {
  flex-flow: row;
}

.tlx-fab-bar--left,
.tlx-fab-bar--left .tlx-fab-bar--link-container {
  flex-flow: row-reverse;
}

.tlx-fab-bar--up,
.tlx-fab-bar--up .tlx-fab-bar--link-container {
  flex-flow: column-reverse;
}

.tlx-fab-bar--down,
.tlx-fab-bar--down .tlx-fab-bar--link-container {
  flex-flow: column;
}

.tlx-fab-bar--label {
  font-size: 14px;
}

.tlx-material-fab-button {
  border-radius: 50%;
  padding: 0;
  margin: 0;
  border: 0;
  height: 56px;
  width: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
  text-decoration: none;
  background-color: #db4437;
  opacity: 1;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  outline: none;
  font-size: 24px;
  transition: opacity 0.2s, transform 0.2s;
}

.tlx-material-fab-button:focus, .tlx-material-fab-button:hover {
  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
}

.tlx-material-fab-button:active {
  box-shadow: 0 12px 17px 2px rgba(0, 0, 0, 0.14), 0 5px 22px 4px rgba(0, 0, 0, 0.12), 0 7px 8px -4px rgba(0, 0, 0, 0.2);
}

.tlx-material-fab-button.secondary {
  background-color: blue;
  height: 40px;
  width: 40px;
  margin: 8px;
}

.tlx-fab-bar .tlx-material-fab-button.primary .material-icons {
  transform: rotateZ(-45deg);
  transition: transform 0.2s;
}

.tlx-fab-bar--open .tlx-material-fab-button.primary .material-icons {
  transform: rotateZ(0);
}

.tlx-fab-bar.tlx-fab-bar--closed .tlx-material-fab-button.secondary {
  opacity: 0;
  pointer-events: none;
  transform: scaleX(0.5) scaleY(0.5);
}

@media screen and (max-width: 460px), screen and (max-height: 460px) {
  .tlx-material-fab-button.primary {
    width: 40px;
    height: 40px;
  }
}

.tlx-fab-bar--menu .tlx-fab-bar--link-container {
  display: block;
  clip-path: circle(0 at center);
  transition: clip-path 0.25s;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.2);
}

.tlx-fab-bar--menu.tlx-fab-bar--open.tlx-fab-bar--primaryActive .tlx-fab-bar--link-container {
  clip-path: circle(200px at center);
}

.tlx-fab-bar--menu .tlx-material-fab-button.primary {
  position: absolute;
  bottom: 0;
  right: 0;
  transition: top 0.2s, bottom 0.2s, left 0.2s, right 0.2s;
}

.tlx-fab-bar--menu .tlx-material-fab-button.primary .tlx-fab-bar--label {
  display: none;
}

.tlx-fab-bar--menu.tlx-fab-bar--open .tlx-material-fab-button.primary {
  right: 50px;
  bottom: 50px;
}

.tlx-fab-bar.tlx-fab-bar--menu .tlx-material-fab-button.secondary {
  background-color: white;
  z-index: -1;
  height: 56px;
  width: auto;
  margin: 0;
  border-radius: 0;
  box-shadow: none;
  color: black;
  justify-content: flex-start;
  padding-right: 16px;
  opacity: 1;
  transition: none;
  transform: none;
}

.tlx-fab-bar.tlx-fab-bar--menu .tlx-material-fab-button.secondary:last-child {
  background-color: #db4437;
  color: white;
}

.tlx-fab-bar--menu .tlx-fab-bar--icon {
  display: inline-block;
  padding: 16px;
}

.tlx-fab-bar--toolbar .tlx-fab-bar--link-container {
  position: fixed;
  width: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: #db4437;
  overflow: hidden;
  transition: width 0.2s;
  transition-delay: 0.1s;
  display: flex;
  justify-content: space-around;
}

.tlx-fab-bar--toolbar .tlx-material-fab-button.primary {
  position: fixed;
  bottom: 24px;
  right: 24px;
  transition: bottom 0.2s, right 0.2s;
}

.tlx-fab-bar--toolbar .tlx-material-fab-button.secondary {
  transition: none;
  background-color: #db4437;
  box-shadow: none;
}

.tlx-fab-bar--toolbar.tlx-fab-bar--open .tlx-fab-bar--link-container {
  width: 100vw;
}

.tlx-fab-bar--toolbar.tlx-fab-bar--open .tlx-material-fab-button.primary {
  bottom: 0;
  right: calc(50% - 28px);
}

.tlx-fab-bar--toolbar.tlx-fab-bar--open .tlx-fab-bar--link-container {
  transition-delay: 0;
}

.tlx-fab-bar--toolbar.tlx-fab-bar--closed .tlx-material-fab-button.primary {
  transition-delay: 0.25s;
}

.tlx-fab-bar.upperLeft {
  top: 24px;
  left: 24px;
}

.tlx-fab-bar.upperRight {
  top: 24px;
  right: 24px;
}

.tlx-fab-bar.lowerLeft {
  bottom: 24px;
  left: 24px;
}

.tlx-fab-bar.lowerRight {
  bottom: 24px;
  right: 24px;
}
